function validatePwd(id, pwd) {
    return axios.post("/download/" + id, {
        pwd: pwd
    }).then(data => data.data.records).then(data => {
        // console.log(data);

        return data.length !== 0 ? data[0].fields.attachments[0] : [];
    })
}

